@import url(https://fonts.cdnfonts.com/css/cubano);
@import url(https://fonts.cdnfonts.com/css/sofia-pro);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Sofia Pro';
    color: cyan;
}




.home {
    min-height: 100vh;
    background: url(/static/media/wallpaper.553bde5c.jpg);
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.6);
    background-blend-mode: multiply;
    display: grid;
    place-items: center;
    color: white;
    text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3);
}

.avatar-wrapper {
    border-radius: 50%;
    width: 20em;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
        rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

.avatar-wrapper img {
    border-radius: inherit;
    width: inherit;
}

.hover:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.hover {
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
}

.home-title {
    font-family: 'Cubano';
    font-size: 2em;
    font-weight: 600;
    text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3);
}

.avatar-name {
    font-family: 'Cubano';
    color: yellow;
}

.home-information {
    display: flex;
    flex-direction: column;
    text-align: center;
    grid-gap: 1em;
    gap: 1em;
}

.typewriter-text {
    font-weight: bold;
    font-size: 0.9em;
}

.home-desc {
    text-align: center;
    font-size: 2em;
}

@media (max-width: 550px) {
    .typewriter-text {
        font-size: 0.8em;
    }
}

/* Waving Hand https://codepen.io/jakejarvis/pen/pBZWZw */

.wave {
    -webkit-animation-name: wave-animation;
            animation-name: wave-animation; /* Refers to the name of your @keyframes element below */
    -webkit-animation-duration: 2.5s;
            animation-duration: 2.5s; /* Change to speed up or slow down */
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite; /* Never stop waving :) */
    -webkit-transform-origin: 70% 70%;
            transform-origin: 70% 70%; /* Pivot around the bottom-left palm */
    display: inline-block;
}

.home-social-links {
    display: flex;
    grid-gap: 1em;
    gap: 1em;
    justify-content: center;
}

.social-media-link {
    transition: all 0.5s;
}

.social-media-link:hover {
    -webkit-transform: scale(1.5) rotate(5deg);
            transform: scale(1.5) rotate(5deg);
    cursor: pointer;
}

.typewriter-text span {
    color: #ff7575;
}

@-webkit-keyframes wave-animation {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    10% {
        -webkit-transform: rotate(14deg);
                transform: rotate(14deg);
    }
    20% {
        -webkit-transform: rotate(-8deg);
                transform: rotate(-8deg);
    }
    30% {
        -webkit-transform: rotate(14deg);
                transform: rotate(14deg);
    }
    40% {
        -webkit-transform: rotate(-4deg);
                transform: rotate(-4deg);
    }
    50% {
        -webkit-transform: rotate(10deg);
                transform: rotate(10deg);
    }
    60% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@keyframes wave-animation {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    10% {
        -webkit-transform: rotate(14deg);
                transform: rotate(14deg);
    }
    20% {
        -webkit-transform: rotate(-8deg);
                transform: rotate(-8deg);
    }
    30% {
        -webkit-transform: rotate(14deg);
                transform: rotate(14deg);
    }
    40% {
        -webkit-transform: rotate(-4deg);
                transform: rotate(-4deg);
    }
    50% {
        -webkit-transform: rotate(10deg);
                transform: rotate(10deg);
    }
    60% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

.projects {
    padding-top: 5em;
    background: url(/static/media/wallpaper3.6bb134cd.jpg);
    min-height: 100vh;
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.6);
    background-blend-mode: multiply;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3);
}

.project {
    display: flex;
    flex-direction: column;
    width: 400px;
    border-radius: 1em;
}

.project:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}

.projects-list {
    padding: 1em;
    padding-top: 4em;
    width: 100%;
    display: grid;
    justify-items: center;
    justify-content: center;
    grid-template-columns: repeat(auto-fill, minmax(450px, 1fr));
    grid-gap: 2em;
    gap: 2em;
}

.project-box {
    background-color: #2a2e35;
    padding: 1em;
    display: flex;
    border-bottom-left-radius: inherit;
    border-bottom-right-radius: inherit;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.project-image {
    width: inherit;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
        rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
}

.project-info {
    display: flex;
    grid-gap: 1em;
    gap: 1em;
    flex-direction: column;
}

.project-tech {
    font-size: 0.85em;
    padding: 0.5em;
    border-radius: 0.75em;
    color: black;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
        rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
    text-shadow: none;
}

.project-title {
    font-family: 'Cubano';
}

.project-stack {
    display: flex;
    grid-gap: 0.5em;
    gap: 0.5em;
}

.project-tech.html {
    background-color: rgb(52, 178, 52);
}

.project-tech.css {
    background-color: rgb(79, 79, 251);
    color: white;
}

.project-tech.react {
    background-color: rgb(66, 75, 78);
    color: rgb(19, 222, 212);
}

.project-tech.firebase {
    background-color: orange;
}

.project-tech.javascript {
    background-color: rgb(236, 249, 90);
    color: black;
}

.project-buttons {
    display: flex;
    grid-gap: 1em;
    gap: 1em;
}

.project-button {
    font-family: 'Cubano';
    border-radius: 0.75em;
    background-color: black;
    padding: 0.5em 1.5em;
}

.project-desc {
    color: lightgrey;
}

.projects-main-title {
    font-family: 'Cubano';
    font-size: 2em;
}

.project-button:hover {
    cursor: pointer;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: all 0.3s;
}
@media (max-width: 550px) {
    .project {
        width: 300px;
    }
    .project-tech {
        font-size: 0.8em;
    }
}

.about {
    background: url(/static/media/wallpaper2.9928807a.jpg);
    min-height: 200vh;
    background-size: cover;
    background-color: rgba(0, 0, 0, 0.6);
    background-blend-mode: multiply;
    background-position: center;
    display: grid;
    place-items: center;
    color: white;
    text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3);
}

.about-information {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 5em;
    gap: 5em;
    width: 100%;
}
.card-information,
.skills-information {
    display: flex;
    flex-direction: column;
    grid-gap: 1em;
    gap: 1em;
    width: 60vh;
}

.skills-information {
    width: 100vh;
}

.card-title {
    font-family: 'Cubano';
    text-align: center;
    font-size: 2em;
}

.card-desc {
    font-size: 1.25em;
    color: white;
    text-align: justify;
}

.card-hobbies {
    padding-left: 3em;
}

.card-hobby {
    color: white;
}

.highlight {
    color: rgb(236, 249, 90);
}

.strikethrough:hover {
    color: red;
    text-decoration: line-through;
}

.skill-cards {
    display: grid;
    grid-gap: 2em;
    gap: 2em;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    justify-items: center;
}

.skill-card {
    border: 2px solid rgb(150, 35, 165);
    font-size: 5em;
    padding: 0.5em 0.8em;
    width: 2.5em;
    border-radius: 0.1em;
}

.skills-scroll {
    text-align: center;
    font-weight: bold;
    color: rgb(207, 73, 225);
    cursor: pointer;
    font-family: 'Cubano';
}

.skill-card:hover {
    border: 2px solid rgb(215, 117, 228);
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: all 0.25s ease;
}

@media (max-width: 420px) {
    .skill-cards {
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    }
}

@media (max-width: 1024px) {
    .skill-cards {
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    }

    .skills-information {
        width: 45vh;
    }

    .card-information {
        width: 45vh;
    }
}

@media (max-width: 550px) {
    .card-information,
    .skills-information {
        width: 50vh;
    }
    .about {
        height: 220vh;
    }
    html {
        font-size: 75%;
    }

    .skill-card {
        width: 2.25em;
    }
    .skill-cards {
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    }
}

@media (max-width: 480px) {
    .card-information,
    .skills-information {
        width: 40vh;
    }

    .card-information {
        width: 80%;
    }
    .skill-cards {
        grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    }
}
svg {
    height: auto;
    width: auto;
}

.navbar {
    display: flex;
    position: fixed;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.3);
    padding: 0.5em;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px,
        rgba(0, 0, 0, 0.23) 0px 6px 6px;
    width: 100%;
    font-weight: bold;
    text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.3);
    font-size: 1.2em;
}

.navbar-button,
.hamburger-button {
    display: flex;
    align-items: center;
    grid-gap: 0.25em;
    gap: 0.25em;
    color: white;
    -webkit-user-select: none;
            user-select: none;
    cursor: pointer;
    font-family: 'Cubano';
}

.un {
    padding-bottom: 5px;
    background-image: linear-gradient(rgb(0, 221, 255) 0 0);
    background-position: 0 100%; /*OR bottom left*/
    background-size: 0% 5px;
    background-repeat: no-repeat;
    transition: background-size 0.4s, background-position 0s 0.4s; /*change after the size immediately*/
}

.un:hover {
    background-position: 100% 100%; /*OR bottom right*/
    background-size: 100% 5px;
}

.navbar-links {
    display: flex;
    grid-gap: 2em;
    gap: 2em;
}

.navbar-name {
    font-family: 'Cubano';
    -webkit-user-select: none;
            user-select: none;
    padding-left: 1em;
}

.MuiDrawer-paper {
    background-color: rgba(0, 0, 0, 0.8) !important;
}
.nav-expand-less {
    display: flex;
    align-self: flex-end;
    padding: 0.5em;
}

.empty-icon {
    width: 40px;
    height: 40px;
}

